import { useCallback } from "react"
import { useSearchParams } from "react-router-dom"

export const useWatchlistsFilterTerm = () => {
  const [searchParams, setSearchParams] = useSearchParams()
  const filterTerm = searchParams.get("find") ?? ""
  const setFilterTerm = useCallback(
    (term: string) => {
      setSearchParams(
        (prev: URLSearchParams & { find?: string }) => {
          const { find: _, ...rest } = Object.fromEntries(prev)
          return term.length ? { ...rest, find: term } : rest
        },
        { replace: true },
      )
    },
    [setSearchParams],
  )
  return { filterTerm, setFilterTerm }
}
